import { graphql } from "gatsby"
import { GatsbyImage } from "gatsby-plugin-image"
import * as React from "react"
import { NavLink, Flex, Container } from "theme-ui"
import Layout from "../components/layout"


const ProjectsPage = ({data}) => {
    return (
        <Layout pageTitle="Projects">
            <Container sx={{textAlign: "center"}}><h3>Overview of selected projects that I have worked on:</h3></Container>
            <Flex sx={{flexWrap: "wrap"}}>
                {data.allMdx.nodes.map(proj => (
                  <NavLink href={`/${proj.slug}`}>
                    <Flex p={2} key={proj.id} sx={{
                      flexDirection: "column",
                      textAlign: "center",
                      boxShadow: '0 0 8px rgba(0, 0, 0, 0.125)',
                      margin: 10,
                      width: 250, height: 275
                    }}>
                      <GatsbyImage image={proj.frontmatter.thumbnail.childImageSharp.gatsbyImageData} />
                      <div>{proj.frontmatter.title}</div>
                    </Flex>
                  </NavLink>
                ))}
            </Flex>
        </Layout>
    )
}

export const query = graphql`
query {
  allMdx(sort: {fields: frontmatter___order, order: ASC}, filter: {slug: {glob: "projects/*"}}) {
    nodes {
      frontmatter {
        title
        thumbnail {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
      id
      slug
    }
  }
}
`;

export default ProjectsPage;